
.book-button{
    background: url("https://assets-global.website-files.com/5db1f399eb19793b28a7982e/5e98a7bdef627d84c85c1ada_arrow%20(9).svg") no-repeat 100% 55% ;
    background-size: 12px;
    background-position-x: 110px;
    margin-right: 10%;
    padding: 14px 45px 12px 15px !important;
    background-size: 12px !important;
    font-size: 13px !important;
    line-height: 14px !important;
    border: 0px !important;
    font-weight: bold !important;
    background-color:#07C29F !important;
    
}

.purple-btn{
    
    background-color:  #8D469C !important;
}
.dropdown-content{
    overflow: hidden;
    display: none;
    position: absolute;
    top: 30px;
    left: -50px;
    font-size: 11px;
    width: 250px;
    height: auto;
    padding: 15px 15px ;
    border-radius: 10px;
    background-color: white;


}
.dropdown-content a{
    display: block;
    color: black;
    
}
.dropdown{
    position: relative;
}
.navLink:hover .dropdown-content{
    display:inline-block;
    /* position: */
   

}
.dropdown-content a:hover{
    color: black;
}
.navLink:hover a {
    text-decoration: none;
}
.nav-link{
    margin-left: 55%;
    font-size: 15px;
    font-weight: bold;
    color: white !important;
}
.nav-link a {
    padding: 10px 0px;
    margin-left: 20px;
    margin-right: 20px;
    
}
.nav-logo{
    height: 70px;
    width: 150px;

   }
.purple-logo{
    background-image: url("https://assets-global.website-files.com/5db1f399eb19793b28a7982e/5e8272e431ada098e103a6d8_Logo.svg") !important ;
    background-repeat: no-repeat;
    background-position: center;
}
.white-logo{
    background-image: url("https://assets-global.website-files.com/5db1f399eb19793b28a7982e/5e7d03d8e80b96bb912d3f25_pickle-logo-white.svg") ;
    background-repeat: no-repeat;
    background-position: center;
}


.default{
    color: #8D469C !important;
}
